function translate(key) {
  switch(key) {
    case 'power_generation': return 'Power:';
    case 'daily_hull_regen': return 'Hull regeneration:';
    case 'daily_armor_regen': return 'Armor regeneration:';
    case 'orbital_bombardment_damage': return 'Bombardment damage:';
    case 'damage_per_hit': return 'Damage:';
    case 'def_platform_cost': return 'Platform cost:';
    case 'def_platform_power': return 'Platform power:';
    case 'emergency_jump_cooldown': return 'Jump cooldown:';
    case 'combat_disengagement_chance': return 'Disengagement chance';
    default: return `${(key.charAt(0).toUpperCase() + key.slice(1)).replace(/_/gi, ' ')}:`
  }
}

export default translate