import React, {useEffect} from 'react'
import Row from './Row'
import {Link, HashRouter, Switch, Route} from 'react-router-dom'
import WeaponsSvg from './background/weapons-svg'
import UtilitySvg from './background/utility-svg'
import CoreSvg from './background/core-svg'
import weaponComponents from '../data/weapon_components'
import utilityComponents from '../data/utility_components'
import coreComponents from '../data/core_components'
import './Style.css'

function App () {
  useEffect(() => document.getElementById(document.location.hash).classList.add('active'));
  let activeTumbler = (elem) => {
    let links = document.getElementsByClassName('link');
    for (let item of links) {
      item.classList.remove('active')
    }
    elem.classList.add('active')
  }
  return (
    <HashRouter basename='/'>
      <header>
        <Link onClick={(e) => activeTumbler(e.currentTarget)} id='#/' className='link weapon-link' to='/'>Weapon components</Link>
        <Link onClick={(e) => activeTumbler(e.currentTarget)} id='#/utility' className='link utility-link' to='/utility'>Utility components</Link>
        <Link onClick={(e) => activeTumbler(e.currentTarget)} id='#/core' className='link core-link' to='/core'>Core components</Link>
      </header>
      <div id='header-bottom'></div>
      <Switch>
        <Route exact path='/'>
          <Row page={weaponComponents}>
            <WeaponsSvg/>
          </Row>
        </Route>
        <Route exact path='/utility'>
          <Row page={utilityComponents}>
            <UtilitySvg/>
          </Row>
        </Route>
        <Route exact path='/core'>
          <Row page={coreComponents}>
            <CoreSvg/>
          </Row>
        </Route>
      </Switch>
    </HashRouter>
  )
}

export default App