import React from 'react'
import Hex from './Hex'
import './Style.css'

function Card (props) {
  return (
    props.cardContent.map((item, i) =>
    <div className='card' style={{backgroundImage: `${item.bgimg}`}} key={i}>
      <div className='icon' style={{backgroundImage: `${item.icon}`}}></div>
      <p className='text'>{item.text}</p>
      {item.hexes.map((subItem, i) =>
        <Hex modifiers={item} data={subItem} key={i}/>
      )}
    </div>)
  )
}

export default Card