import React from 'react'
import translate from './translator'
import './Style.css'

let weaponModifiers = 'weapon-modifiers';
let componentData = 'value'

function Popup (props) {
  function renderList (data, className) {
    let array = [];
    for (let item in data) {
      if (item === 'class' || item === 'bg' || item === 'type' || item === 'bgimg' || item === 'icon' || item === 'text' || item === 'hexes') {
        continue
      }
      if (Array.isArray(data[item])) {
        array.push(
          <li style={{alignItems: 'center'}} key={Math.random()}>
            <span className='key'>{translate(item)}</span>
            {data[item].map(costItem =>
              <div className='resource-wrap' key={Math.random()}>
                <div className='resource-type' style={{backgroundImage: `${costItem.type}`}}/>
                <span className='value'>{costItem.number}</span>
              </div>
            )}
          </li>
        )
      }
      else {
        array.push(
          <li key={Math.random()}>
            <span className='key'>{translate(item)}</span>
            <span className={className}>{data[item]}</span>
          </li>
        )
      }
    }
    return array
  }
  return (
    <div className={`popup-wrap ${props.indent}`}>
      <ul>
        {renderList(props.data, componentData)}
      </ul>
      <ul>
        {renderList(props.modifiers, weaponModifiers)}
      </ul>
    </div>
  )
}

export default Popup