import React, {useState} from 'react'
import Popup from './Popup'
import './Style.css'

function Hex (props) {
  let [isOpen, setIsOpen] = useState(false);
  let [indent, setIndent] = useState('top');
  return (
    <div 
      className={props.data.class}
      style={{backgroundImage: `${props.data.bg}`}} 
      onMouseEnter={() => setIsOpen(true)} 
      onMouseLeave={() => setIsOpen(false)}
      onMouseMove={(e) => {e.clientY > 500 ? setIndent('bottom') : setIndent('top')}}>
      {isOpen && <Popup modifiers={props.modifiers} data={props.data} indent={indent}/>}
    </div>
  )
}

export default Hex