import React from 'react'
import Card from './Card'
import './Style.css'

function Row (props) {
  return (
    <div className='wrapper'>
      {props.children}
      {props.page.map((item, i) =>
        <div className='row' key={i}>
          <Card cardContent={item}></Card>
        </div>)
      }
    </div>
  )
}

export default Row